body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.row {
  padding-right: 100px;
  padding-left: 100px;
}

div .img {
  border: none;
}

.navbar {
    position: fixed;
    width: 100%;
    z-index: 99;
    white-space: nowrap;
    min-height: 70px;
    text-align: center;
    background-color: #f9f5f0;
}

.navbar li {
    display: inline-block;
    text-align: center;
    padding: 10px;
    width: 40%;
    font-size: 25px;
}

.message {
    margin-right: 30%;
    margin-left: 30%;
} 

.jumbotron {
    padding: 120px;
    text-align: center;
    padding-bottom: 80px;
}
.main {
    margin-bottom: 5%;
}

#characterImage {
    height: 150px;
    width: 150px;
}

.card {
    margin-top: 5%;
}

